import service from "../../service.js";
export default {
	data() {
		return {
			email:'',
			password:'',
			password2:'',
			emailCodes:'',
			tabIndex: 0,
			navList: [
				{
					text: '手机号',
					state: '0'
				},
				{
					text: '邮箱号',
					state: '1'
				}
			],
			status: '获取验证码',
			account:'',
			repwd2:'',
			pwd2:'',
			verifycode:''
		}
	},
	methods: {
		//swiper 切换
		changeTab(e){
			this.tabClick(e.target.current);
		},
		
		//顶部tab点击
		tabClick(index){
			this.change(index);
		},
		change(index){
			this.type = this.navList[index].type;
			this.tabIndex = index;
		},
		jump(url) {
			uni.navigateTo({
				url: url
			})
		},
		count() {
			if (this.status > 0) {
				return
			} else {
				// console.log(22);
				service.auth(this, service.api.reg.code, "GET", {
					'mobile': this.account,
					'temp': 'sms_forget'
				},function(self,res){
					
				self.status = 60
				var timer = setInterval(() => {
					self.status--
					if (self.status == 0) {
						clearInterval(timer)
						self.status = '重新获取'
					};
				}, 1000);
				uni.showToast({
					title:res.info,
					icon:'none'
				})
				})
			};
		},
		// 邮箱验证码
		emailCode(){
			if (this.status > 0) {
				return
			} else {
				// console.log(22);
				service.auth(this, service.api.reg.sendEmailCode, "GET", {
					email: this.email,
				},function(self,res){
					
				self.status = 60
				var timer = setInterval(() => {
					self.status--
					if (self.status == 0) {
						clearInterval(timer)
						self.status = '重新获取'
					};
				}, 1000);
				uni.showToast({
					title:res.info,
					icon:'none'
				})
				})
			};
			},
		btns(){
			service.auth(this , service.api.set.forget2 , 'POST' , {
				verifycode:this.verifycode,
				repwd2:this.repwd2,
				pwd2:this.pwd2,
				account:this.account,
				type:2
			} , function(self , res){
				uni.showToast({
					title:res.info,
					icon:'none'
				});
				setTimeout(()=>{
					uni.switchTab({
						url:"/pages/main/my"
					})
				},1500)
			})
		},
		// 邮箱确认按钮
		emailBtns(){
			service.auth(this , service.api.set.forget2 , 'POST' , {
				verifycode:this.emailCodes,
				repwd2:this.password2,
				pwd2:this.password,
				email:this.email,
				type:1
			} , function(self , res){
				uni.showToast({
					title:res.info,
					icon:'none'
				});
				setTimeout(()=>{
					uni.switchTab({
						url:"/pages/main/my"
					})
				},1500)
			})
		}
	},
}
